import { createTheme, ThemeProvider } from '@mui/material/styles';

import { BsPersonFillAdd } from 'react-icons/bs';

import {
  Typography,
  TextField,
  Button,
  Grid,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Box,
  Slider,
  Autocomplete,
  Paper,
  Chip,
  Avatar,
  Switch,
  FormControlLabel
} from '@mui/material';
import MainCard from 'ui-component/cards/MainCard';
import React, { useState, useEffect } from 'react';
import { styled } from '@mui/material/styles';

const darkTheme = createTheme({
  palette: {
    mode: 'dark'
  }
});

const ListItem = styled('li')(({ theme }) => ({
  margin: theme.spacing(0.5)
}));

const marks = [
  {
    value: 25,
    label: '25'
  },
  {
    value: 100,
    label: '100'
  },
  {
    value: 250,
    label: '250'
  },
  {
    value: 500,
    label: '500'
  },
  {
    value: 750,
    label: '750'
  },
  {
    value: 1000,
    label: '1000'
  }
];

function valuetext(value) {
  return `${value}ms`;
}

const getNames = async (channels) => {
  if (!localStorage.getItem('login')) return null;

  const idParams = channels.map((channel) => `id=${encodeURIComponent(channel.id)}`).join('&');
  const url = `https://api.twitch.tv/helix/users?${idParams}`;

  const response = await fetch(url, {
    headers: {
      Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
      'Client-ID': JSON.parse(localStorage.getItem('login')).client_id
    }
  });

  const data = await response.json();

  if (!response.ok) {
    throw new Error(data.message ? `${data.error}, ${data.message}` : response.statusText);
  }

  const names = data.data.map((user) => ({ id: user.id, name: user.login, pfp: user.profile_image_url.replace('300x300', '50x50') }));
  return names;
};

function chunkArray(array, size) {
  let result = [];
  for (let i = 0; i < array.length; i += size) {
    let chunk = array.slice(i, i + size);
    result.push(chunk);
  }
  return result;
}

const Page = () => {
  const [message, setMessage] = useState('');
  const [outputData, setOutputData] = useState(null);
  const [outputError, setOutputError] = useState(null);
  const [selectedType, setSelectedType] = useState('');
  const [channel, setChannel] = useState('');
  const [onlyOfflineChannelsValue, setOnlyOfflineChannelsValue] = useState(true);
  const [sliderValue, setSliderValue] = useState(100);
  const [searchResults, setSearchResults] = useState([]);
  const [chipData, setChipData] = useState([]);

  const handleDelete = (chipToDelete) => {
    localStorage.setItem(
      'crossbanChannels',
      JSON.stringify(JSON.parse(localStorage.getItem('crossbanChannels')).filter((chip) => chip.id !== chipToDelete.id))
    );
    setChipData((chips) => chips.filter((chip) => chip.id !== chipToDelete.id));
  };

  useEffect(() => {
    document.title = `LsCo Software - Crossban`;

    const login = localStorage.getItem('login') || null;

    if (!login) {
      setOutputData('Log in to get started.');
      setOutputError(null);
    }
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      const storedChannels = localStorage.getItem('crossbanChannels');
      if (storedChannels) {
        const parsedChannels = JSON.parse(storedChannels);
        const names = await getNames(parsedChannels);
        setChipData(names ?? []);
      }
    };

    fetchData();
  }, []);

  async function banUser(channelID, users) {
    try {
      await Promise.all(
        chunkArray(users, 100).map(async (chunk) => {
          const response = await fetch(`https://api.twitch.tv/helix/users?login=${chunk.map((data) => data.login).join('&login=')}`, {
            headers: {
              Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
              'Client-ID': JSON.parse(localStorage.getItem('login')).client_id
            }
          });
          const data = await response.json();
          if (!response.ok) throw new Error(`API Error -> Status: ${data.error}, ${data.status} - ${data.message}`);

          data.data.forEach((user) => {
            const existingUser = users.find((u) => u.login === user.login);
            if (existingUser) existingUser.id = user.id;
          });

          return data.data.map((user) => user.id);
        })
      );

      for (let i = 0; i < users.length; i++) {
        const data = users[i];

        fetch(
          `https://api.twitch.tv/helix/moderation/bans?broadcaster_id=${channelID}&moderator_id=${
            JSON.parse(localStorage.getItem('login')).id
          }`,
          {
            method: 'POST',
            headers: {
              Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
              'Client-ID': JSON.parse(localStorage.getItem('login')).client_id,
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({ data: { user_id: data.id, reason: data.reason } })
          }
        );

        await new Promise((x) => setTimeout(x, sliderValue));
      }
    } catch (error) {
      console.log(error);
      throw new Error(`Error: ${error.message}`);
    }
  }

  async function timeoutUser(channelID, users) {
    try {
      await Promise.all(
        chunkArray(users, 100).map(async (chunk) => {
          const response = await fetch(`https://api.twitch.tv/helix/users?login=${chunk.map((data) => data.login).join('&login=')}`, {
            headers: {
              Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
              'Client-ID': JSON.parse(localStorage.getItem('login')).client_id
            }
          });
          const data = await response.json();
          if (!response.ok) throw new Error(`API Error -> Status: ${data.error}, ${data.status} - ${data.message}`);

          data.data.forEach((user) => {
            const existingUser = users.find((u) => u.login === user.login);
            if (existingUser) existingUser.id = user.id;
          });

          return data.data.map((user) => user.id);
        })
      );

      for (let i = 0; i < users.length; i++) {
        const data = users[i];

        fetch(
          `https://api.twitch.tv/helix/moderation/bans?broadcaster_id=${channelID}&moderator_id=${
            JSON.parse(localStorage.getItem('login')).id
          }`,
          {
            method: 'POST',
            headers: {
              Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
              'Client-ID': JSON.parse(localStorage.getItem('login')).client_id,
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({ data: { user_id: data.id, duration: Number(data.duration), reason: data.reason } })
          }
        );

        await new Promise((x) => setTimeout(x, sliderValue));
      }
    } catch (error) {
      console.log(error);
      throw new Error(`Error: ${error.message}`);
    }
  }

  async function banUser_id(channelID, users) {
    try {
      for (let i = 0; i < users.length; i++) {
        const data = users[i];

        fetch(
          `https://api.twitch.tv/helix/moderation/bans?broadcaster_id=${channelID}&moderator_id=${
            JSON.parse(localStorage.getItem('login')).id
          }`,
          {
            method: 'POST',
            headers: {
              Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
              'Client-ID': JSON.parse(localStorage.getItem('login')).client_id,
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({ data: { user_id: data.id, reason: data.reason } })
          }
        );

        await new Promise((x) => setTimeout(x, sliderValue));
      }
    } catch (error) {
      console.log(error);
      throw new Error(`Error: ${error.message}`);
    }
  }

  async function timeoutUser_id(channelID, users) {
    try {
      for (let i = 0; i < users.length; i++) {
        const data = users[i];

        fetch(
          `https://api.twitch.tv/helix/moderation/bans?broadcaster_id=${channelID}&moderator_id=${
            JSON.parse(localStorage.getItem('login')).id
          }`,
          {
            method: 'POST',
            headers: {
              Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
              'Client-ID': JSON.parse(localStorage.getItem('login')).client_id,
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({ data: { user_id: data.id, duration: Number(data.duration), reason: data.reason } })
          }
        );

        await new Promise((x) => setTimeout(x, sliderValue));
      }
    } catch (error) {
      console.log(error);
      throw new Error(`Error: ${error.message}`);
    }
  }

  async function unbanUser(channelID, users) {
    try {
      await Promise.all(
        chunkArray(users, 100).map(async (chunk) => {
          const response = await fetch(`https://api.twitch.tv/helix/users?login=${chunk.map((data) => data.login).join('&login=')}`, {
            headers: {
              Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
              'Client-ID': JSON.parse(localStorage.getItem('login')).client_id
            }
          });
          const data = await response.json();
          if (!response.ok) throw new Error(`API Error -> Status: ${data.error}, ${data.status} - ${data.message}`);

          data.data.forEach((user) => {
            const existingUser = users.find((u) => u.login === user.login);
            if (existingUser) existingUser.id = user.id;
          });

          return data.data.map((user) => user.id);
        })
      );

      for (let i = 0; i < users.length; i++) {
        const data = users[i];

        fetch(
          `https://api.twitch.tv/helix/moderation/bans?broadcaster_id=${channelID}&moderator_id=${
            JSON.parse(localStorage.getItem('login')).id
          }&user_id=${data.id}`,
          {
            method: 'DELETE',
            headers: {
              Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
              'Client-ID': JSON.parse(localStorage.getItem('login')).client_id,
              'Content-Type': 'application/json'
            }
          }
        );

        await new Promise((x) => setTimeout(x, sliderValue));
      }
    } catch (error) {
      console.log(error);
      throw new Error(`Error: ${error.message}`);
    }
  }

  async function unbanUser_id(channelID, users) {
    try {
      for (let i = 0; i < users.length; i++) {
        const data = users[i];

        fetch(
          `https://api.twitch.tv/helix/moderation/bans?broadcaster_id=${channelID}&moderator_id=${
            JSON.parse(localStorage.getItem('login')).id
          }&user_id=${data.id}`,
          {
            method: 'DELETE',
            headers: {
              Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
              'Client-ID': JSON.parse(localStorage.getItem('login')).client_id,
              'Content-Type': 'application/json'
            }
          }
        );
        await new Promise((x) => setTimeout(x, sliderValue));
      }
    } catch (error) {
      console.log(error);
      throw new Error(`Error: ${error.message}`);
    }
  }

  const getLiveChannels = async (channels) => {
    const request = await fetch(`https://api.twitch.tv/helix/streams?user_id=${channels.map((data) => data.id).join('&user_id=')}`, {
      headers: {
        Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
        'Client-ID': JSON.parse(localStorage.getItem('login')).client_id
      }
    });

    const data = await request.json();

    if (!request.ok) return;

    return data.data;
  };

  const handleSearchChange = async (value) => {
    if (!localStorage.getItem('login')) {
      return setSearchResults([]);
    }

    if (value.trim() !== '') {
      const response = await fetch(`https://api.twitch.tv/helix/search/channels?query=${value}&first=25`, {
        headers: {
          Authorization: 'Bearer ' + JSON.parse(localStorage.getItem('login')).access_token,
          'Client-ID': JSON.parse(localStorage.getItem('login')).client_id
        }
      });

      const data = await response.json();

      if (response.ok) {
        setSearchResults(
          data.data.map((channel) => {
            return {
              name: channel.display_name,
              id: channel.id,
              avatarUrl: channel.thumbnail_url.replace('300x300', '50x50')
            };
          })
        );
      } else {
        setSearchResults([]);
      }
    } else {
      setSearchResults([]);
    }
  };

  const handleClick = async () => {
    if (!localStorage.getItem('login')) {
      setOutputData(null);
      setOutputError('Log in to get started.');
      return;
    }

    if (!chipData.length) {
      setOutputData(null);
      setOutputError('You must specify a channel');
      return;
    }

    if (!selectedType) {
      setOutputData(null);
      setOutputError('You must specify a filesay type');
      return;
    }

    if (!message.trim()) {
      setOutputData(null);
      setOutputError('You must specify a filesay list');
      return;
    }

    try {
      const text = message.split(/\r?\n/).map((x) => x.trim());
      let type;

      if (text.length >= 500) {
        setOutputData(null);
        setOutputError('You have too many lines.');
        return;
      }

      setOutputData(null);
      setOutputError(null);

      if (selectedType === 'ban-list-id') {
        type = 4;
      } else if (selectedType === 'unban-list-id') {
        type = 3;
      } else if (selectedType === 'ban-list-name') {
        type = 2;
      } else if (selectedType === 'unban-list-name') {
        type = 1;
      } else {
        type = 0;
      }

      const messages = [];
      const timeouts = [];
      const timeouts_id = [];
      const unbans = [];
      const bans = [];
      const unbans_id = [];
      const bans_id = [];

      for (let i = 0; i < text.length; i++) {
        const line = text[i];
        if (line) {
          switch (type) {
            case 0: {
              switch (true) {
                case line.match(/^(\.|\/)timeout\s\w+\s(\d+)(\s.+)?/i) !== null: {
                  const [, login, duration, ...reason] = line.split(/\s+/);
                  if (!timeouts.find((x) => x.login === login.toLowerCase())) {
                    timeouts.push({ login: login.toLowerCase(), duration: duration, reason: reason.join(' ') || '' });
                  }
                  break;
                }
                case line.match(/^(\.|\/)ban\s\w+(\s.+)?/i) !== null: {
                  const [, login, ...reason] = line.split(/\s+/);
                  if (!bans.find((x) => x.login === login.toLowerCase())) {
                    bans.push({ login: login.toLowerCase(), reason: reason.join(' ') || '' });
                  }
                  break;
                }
                case line.match(/^(\.|\/)unban\s\w+/i) !== null || line.match(/^(\.|\/)untimeout\s\w+/i) !== null: {
                  const [, login] = line.split(/\s+/);
                  if (!unbans.find((x) => x.login === login.toLowerCase())) {
                    unbans.push({ login: login.toLowerCase() });
                  }
                  break;
                }
                case line.match(/^(\.|\/)timeoutid\s\w+\s(\d+)(\s.+)?/i) !== null: {
                  const [, id, duration, ...reason] = line.split(/\s+/);
                  if (!timeouts_id.find((x) => x.id === id)) {
                    timeouts_id.push({ id: id, duration: duration, reason: reason.join(' ') || '' });
                  }
                  break;
                }
                case line.match(/^(\.|\/)banid\s\w+(\s.+)?/i) !== null: {
                  const [, id, ...reason] = line.split(/\s+/);
                  if (!bans_id.find((x) => x.id === id)) {
                    bans_id.push({ id: id, reason: reason.join(' ') || '' });
                  }
                  break;
                }
                case line.match(/^(\.|\/)unbanid\s\w+/i) !== null || line.match(/^(\.|\/)untimeoutid\s\w+/i) !== null: {
                  const [, id] = line.split(/\s+/);
                  if (!unbans_id.find((x) => x.id === id)) {
                    unbans_id.push({ id: id });
                  }
                  break;
                }
                default: {
                  messages.push({ message: line });
                  break;
                }
              }
              break;
            }
            case 1: {
              const [login] = line.split(/\s+/);
              if (!unbans.find((x) => x.login === login.toLowerCase())) {
                unbans.push({ login: login.toLowerCase() });
              }
              break;
            }
            case 2: {
              const [login, ...reason] = line.split(/\s+/);
              if (!bans.find((x) => x.login === login.toLowerCase())) {
                bans.push({
                  login: login.toLowerCase(),
                  reason: reason ? reason.join(' ') : `Ban-list`
                });
              }
              break;
            }
            case 3: {
              const [id] = line.split(/\s+/);
              if (!unbans.find((x) => x.id === id)) {
                unbans_id.push({ id: id });
              }
              break;
            }
            case 4: {
              const [id, ...reason] = line.split(/\s+/);
              if (!bans.find((x) => x.id === id)) {
                bans_id.push({ id: id, reason: reason ? reason.join(' ') : `Ban-list` });
              }
              break;
            }
            default: {
              break;
            }
          }
        }
      }

      console.log('Bans', bans, bans.length);
      console.log('Unbans', unbans, unbans.length);
      console.log('Bans (ID)', bans_id, bans_id.length);
      console.log('Unbans (ID)', unbans_id, unbans_id.length);
      console.log('Timeouts', timeouts, timeouts.length);
      console.log('Timeouts (ID)', timeouts_id, timeouts_id.length);
      console.log('Messages', messages, messages.length);

      const liveChanels = await getLiveChannels(chipData);

      for (let i = 0; i < chipData.length; i++) {
        const channelData = chipData[i];
        if (
          onlyOfflineChannelsValue &&
          liveChanels.find((i) => {
            console.log(i.user_id === channelData.id);
            return i.user_id === channelData.id;
          })
        )
          continue;
        await Promise.all([
          banUser(channelData.id, bans),
          unbanUser(channelData.id, unbans),
          banUser_id(channelData.id, bans_id),
          unbanUser_id(channelData.id, unbans_id),
          timeoutUser(channelData.id, timeouts),
          timeoutUser_id(channelData.id, timeouts_id)
        ]);

        if (messages.length) {
          const ws = new WebSocket('wss://irc-ws.chat.twitch.tv');
          ws.onopen = async () => {
            ws.send('CAP REQ :twitch.tv/membership twitch.tv/tags twitch.tv/commands');
            ws.send('PASS oauth:' + JSON.parse(localStorage.getItem('login')).access_token);
            ws.send('NICK ' + JSON.parse(localStorage.getItem('login')).login);

            for (let i = 0; i < messages.length; i++) {
              const message = messages[i].message;
              ws.send(`PRIVMSG #${channelData.name} :${message}`);
              await new Promise((x) => setTimeout(x, sliderValue));

              if (i === messages.length - 1) {
                ws.close();
              }
            }
          };
        }

        await new Promise((x) => setTimeout(x, 500));
      }
    } catch (err) {
      console.log(err);
      setOutputData(null);
      setOutputError(err.message);
      return;
    }
  };

  const addCrossbanChannel = async () => {
    if (!localStorage.getItem('login')) {
      setOutputData(null);
      setOutputError('Log in to get started.');
      return;
    }

    if (channel.name.trim() === '') {
      setOutputData(null);
      setOutputError('You must specify a Crossban channel');
      return;
    }

    const channels = localStorage.getItem('crossbanChannels') ? JSON.parse(localStorage.getItem('crossbanChannels')) : [];

    if (channels.find((i) => i.id === channel.id)) {
      setOutputData(null);
      setOutputError(`${channel.name} is already in Crossban list.`);
      return;
    }

    channels.push({
      id: channel.id
    });

    localStorage.setItem('crossbanChannels', JSON.stringify(channels));

    const data = await getNames([
      {
        id: channel.id
      }
    ]);

    const newChipData = [...chipData, { id: channel.id, name: data[0].name, pfp: data[0].pfp }];

    setChipData(newChipData);

    setOutputData(`Successfully added ${channel.name} as Crossban channel.`);
    setOutputError(null);
  };

  const handleSliderChange = (event, newValue) => {
    setSliderValue(newValue);
  };

  return (
    <MainCard title="Crossban" description="Ban/timeout multiple users in multiple channels.">
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={12}>
          <ThemeProvider theme={darkTheme}>
            <Autocomplete
              options={searchResults}
              getOptionLabel={(option) => option.name}
              onChange={(event, value) => setChannel(value)}
              renderInput={(params) => (
                <TextField
                  {...params}
                  fullWidth
                  id="outlined-basic"
                  label="Add Channel"
                  variant="outlined"
                  onChange={(event) => handleSearchChange(event.target.value)}
                />
              )}
              renderOption={(props, option) => (
                <li {...props}>
                  <Box display="flex" alignItems="center">
                    <Avatar src={option.avatarUrl} alt={option.name} sx={{ width: 25, height: 25, marginRight: 1 }} />
                    {option.name}
                  </Box>
                </li>
              )}
            />
          </ThemeProvider>
        </Grid>
        <Grid item xs={12}>
          <Button fullWidth variant="outlined" onClick={addCrossbanChannel} size="large">
            <BsPersonFillAdd size={20} />
          </Button>
        </Grid>
        <Grid item xs={12}>
          <Paper
            sx={{
              display: 'flex',
              justifyContent: 'center',
              flexWrap: 'wrap',
              listStyle: 'none',
              p: 0.5,
              m: 0
            }}
            component="ul"
          >
            {chipData.map((i) => {
              return (
                <ListItem key={i.id} style={{ marginBottom: '0.5rem' }}>
                  <Chip avatar={<Avatar alt="xd" src={i.pfp} />} label={`${i.name} - ID: ${i.id}`} onDelete={() => handleDelete(i)} />
                </ListItem>
              );
            })}
          </Paper>
        </Grid>
        <Grid item xs={12}>
          <ThemeProvider theme={darkTheme}>
            <FormControl fullWidth id="outlined-basic" variant="outlined">
              <InputLabel id="type-label">Type</InputLabel>
              <Select
                label="Type"
                value={selectedType}
                onChange={(e) => setSelectedType(e.target.value)}
                labelId="type-label"
                id="type-select"
              >
                <MenuItem value="filesay">Filesay</MenuItem>
                <MenuItem value="ban-list-name">Ban List (Name)</MenuItem>
                <MenuItem value="unban-list-name">Unban List (Name)</MenuItem>
                <MenuItem value="ban-list-id">Ban List (ID)</MenuItem>
                <MenuItem value="unban-list-id">Unban List (ID)</MenuItem>
              </Select>
            </FormControl>
          </ThemeProvider>
        </Grid>
        <Grid item xs={12}>
          <ThemeProvider theme={darkTheme}>
            <TextField
              fullWidth
              multiline
              rows={10}
              id="outlined-basic"
              label="Text"
              variant="outlined"
              value={message}
              onChange={(e) => setMessage(e.target.value)}
            />
          </ThemeProvider>
        </Grid>
        <Grid item xs={12} style={{ textAlign: 'right' }}>
          Lines. ({message.split('\n').length}/500)
        </Grid>
        <ThemeProvider theme={darkTheme}>
          <Grid item xs={12}>
            <Typography gutterBottom>Output speed in ms (Milliseconds)</Typography>
            <FormControl fullWidth variant="filled">
              <Slider
                aria-label="Delay"
                value={sliderValue}
                onChange={handleSliderChange}
                getAriaValueText={valuetext}
                step={null}
                valueLabelDisplay="auto"
                marks={marks}
                min={marks[0].value}
                max={marks[marks.length - 1].value}
              />
            </FormControl>
          </Grid>
        </ThemeProvider>
        <Grid item xs={12} sm={2}>
          <FormControlLabel
            style={{ textAlign: 'center' }}
            value={onlyOfflineChannelsValue}
            control={<Switch color="info" onChange={(e) => setOnlyOfflineChannelsValue(e.target.checked)} defaultChecked />}
            label="Only offline channels?"
            labelPlacement="start"
          />
        </Grid>
        <Grid item xs={12} sm={10}>
          <Button variant="contained" fullWidth onClick={handleClick}>
            Send
          </Button>
        </Grid>
        <Grid item xs={12}>
          {outputError && (
            <Typography variant="body2" style={{ color: 'red', marginTop: '10px', textAlign: 'center' }}>
              {outputError}
            </Typography>
          )}
          {outputData && (
            <Typography variant="body2" style={{ marginTop: '10px', textAlign: 'center' }}>
              {outputData}
            </Typography>
          )}
        </Grid>
      </Grid>
    </MainCard>
  );
};

export default Page;
